
/* 
   @WebSocketMessage
   This message is pushed to the client upon any new output text from remote SSH 
*/
export class SSHSocketOutputResponse {
 
    // Message OpCode (1104) 
    public op: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Payload is the SSH output text 
    public payload: string;
 
    constructor(op?: number, error?: string, payload?: string) { 
        this.op = op;
        this.error = error;
        this.payload = payload;
    }

}


