
/* 
   @WebSocketMessage
   This message is sent from the client to close ssh session using the ssh uri provided in the payload 
*/
export class SSHSocketCloseRequest {
 
    // Message OpCode (1102) 
    public op: number;
 
    // Payload is the SSH connection string URI 
    public payload: string;
 
    constructor(op?: number, payload?: string) { 
        this.op = op;
        this.payload = payload;
    }

}


